.auto-resizable-iframe {
    max-width: 640px;
    height: 390px;
    padding-bottom: 64px;
}

.playerWrapper {
    position: relative;
    padding-bottom: 75%;
}

.auto-resizable-iframe iframe {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
}

@media (max-width: 670px) {
    .outerContainer {
        flex-wrap: wrap-reverse
    }

    .playlistsContainer {
        width: 100%;
    }
}